<template>
	<f7-page>
		<div class="C-detail" :class="classMap[$route.hash%5]" v-if="obj.data">
			<h1 class="C-detail-title">{{obj.data[$route.hash-1].p_title}}</h1>
			<span class="C-detail-date">{{obj.data[$route.hash-1].p_date}}</span>
			<div class="C-detail-content">
				<div v-html="decodeContent($route.hash-1)"></div>
			</div>
		</div>
		<div class="C-detail-close" @click="$route.view.router.back()">
			<i class="iconfont icon-guanbi"></i>
		</div>
	</f7-page>
</template>
<script>
	const Base64 = require('js-base64').Base64
	export default {
		data() {
			return {
				obj: {},
			}
		},
		created() {
			this.classMap = ['C-color-4', 'C-color-3', 'C-color-2', 'C-color-1', 'C-color-5'];
			let resObj = JSON.parse(Base64.decode(sessionStorage.getItem("resStr")))
			this.obj = resObj
		},
		methods: {
			decodeContent(id) {
				return Base64.decode(JSON.parse(Base64.decode(sessionStorage.getItem("resStr"))).data[id].p_content)
			},
		}
	}
</script>
<style>
	@import "../common/css/codehl.min.css";
</style>


