<template>
	<div class="C-main" id="app">
		<f7-statusbar></f7-statusbar>
		<f7-views>
			<f7-view>
				<f7-pages>
					<f7-page>
						<div v-if="obj.data">
							<div class="C-posts-list" v-for="item in obj.data">
								<div class="C-post" :class="classMap[item.p_id%5]">
									<div class="C-card">
										<div class="C-card-item C-title">
											<span class="C-card-title">{{item.p_title}}</span>
										</div>
										<div class="C-card-item C-card-date">
											{{item.p_date}}
										</div>
										<div class="C-card-item C-card-description">
											{{item.p_description}}
										</div>
										<div class="C-card-item">
											<a :href="'/post/#'+item.p_id">
												<button class="C-card-button">阅读全文</button>
											</a>
										</div>
									</div>
								</div>
							</div>
						</div>
					</f7-page>
				</f7-pages>
			</f7-view>
		</f7-views>
	</div>
</template>

<script>
	const Base64 = require('js-base64').Base64
	let url = 'https://ceshu.github.io/data/posts/v1/posts.json'
	let axiosConfig = {
		method: 'get',
		url: url,
	}
	export default {
		data() {
			return {
				obj: {}
			}
		},
		created() {
			this.classMap = ['C-color-4', 'C-color-3', 'C-color-2', 'C-color-1', 'C-color-5'];
			this.$http(axiosConfig).then((res) => {
				res = res.data
				let resStrSS = Base64.encode(JSON.stringify(res))
				sessionStorage.setItem("resStr", resStrSS)
				res.data.reverse()
				this.obj = res
			})
		}
	}
</script>


